 import lotusAddress from "@/components/Winglau14-lotusAddress/Winglau14-lotusAddress.vue";
 import pop from '@/components/promptOptions/pop'
 export default{
	 data(){
		 return{
			 // 性别
			 sexList:[
				{name:'男'}, 
				{name:'女'},
				{name:'男'},
				{name:'女'},
				{name:'男'},
				{name:'女'}
			 ],
			 // 地区选择
			lotusAddressData:{
			                visible:false,
			                provinceName:'',
			                cityName:'',
			                townName:'',
			            },
			            region:''
		}
	 },
	 components:{
		 lotusAddress,
		 pop
	 },
	 methods:{
		  //打开picker
		         openPicker() {
		             this.lotusAddressData.visible = true;
		             this.lotusAddressData.provinceName = '广东省';
		             this.lotusAddressData.cityName = '广州市';
		             this.lotusAddressData.townName = '白云区';
		         },
		         //回传已选的省市区的值
		         choseValue(res){
		             //res数据源包括已选省市区与省市区code
		             console.log(res);
		             this.lotusAddressData.visible = res.visible;//visible为显示与关闭组件标识true显示false隐藏
		             //res.isChose = 1省市区已选 res.isChose = 0;未选
		             if(res.isChose){
		                 this.lotusAddressData.provinceName = res.province;//省
		                 this.lotusAddressData.cityName = res.city;//市
		                 this.lotusAddressData.townName = res.town;//区
		                 this.region = `${res.province} ${res.city} ${res.town}`; //region为已选的省市区的值
		             }
		         },
				 // 性别选择
				 choosed(m){
					 console.log(m)
					 uni.$off('popChoosed')
				 },
				 openSex(){
					 var that=this
					 uni.$on('popChoosed',function(e){
						 that.choosed(e)
					 })
					 this.$refs.sex.show=true
				 },
				 
	 }
 }
